import * as Yup from "yup";

export const initValues = {
  email: "",
  password: "",
};

export const validationSchema = Yup.object({
  email: Yup.string()
    .email("No es un email vlido")
    .required("El email es requerido")
    .min(5, "No es un email vlido"),
  password: Yup.string()
    .required("La contrasea es requerida")
    .min(7, "La contrasea es muy corta"),
});
